import { createSlice } from "@reduxjs/toolkit";
import axios from "axios";


// On cre une partie d'tat 
export const eventsSlice = createSlice({
  // Nom de la partie d'tat
  name: "events",
  // Etat initial
  initialState: {
    events : null
},
  // Fonctions reducers qui permettent de raliser des intractions avec comme paramtres l'tat actuel et l'action que l'on va effectuer sur cet tat
  reducers: {
    // On cre un vnement
    addEvent: (state, action) => {
      // On cre un nouvel vnement
      const newEvent = {
        // On rcupre les donnes de l'action
        posterId: action.payload.posterId,
        titre: action.payload.titre,
        description: action.payload.description,
        date: action.payload.date,
        nbPlaces: action.payload.nbPlaces,
        picture: action.payload.picture,
        inscrits: action.payload.inscrits,
      };
      // On ajoute cet vnement  l'tat
      state.push(newEvent);
    },
    // On rcupre tous les vnements
    getEvents: (state, action) => {
      // On met  jour l'tat avec les donnes de l'action
      state = action.payload;
    },
    // On supprime un vnement
    deleteEvent: (state, action) => {
      // On filtre l'tat pour ne garder que les vnements dont l'id est diffrent de celui de l'action
      state = state.filter((event) => event._id !== action.payload);
      return state;
    },
    // On modifie un vnement
    updateEvent: (state, action) => {
      state = state.map((event) => {
        if (event._id === action.payload._id) {
          return action.payload;
        } else {
          return event;
        }
      });
    },
  },
});

export const fetchEvent = () => {
  return (dispatch) => {
    return axios
      .get(`http://localhost:4000/api/event/`)
      .then((res) => {
        dispatch({ type: "events/getEvents", payload: res.data });
      })
      .catch((err) => console.log(err));
  };
};

// On exporte les actions
export const { addEvent, getEvents, deleteEvent, updateEvent } = eventsSlice.actions;

// On exporte le reducer
export default eventsSlice.reducer;